<template>
  <div class="wrapper is--search" style="margin-bottom: 0.2rem;">
    <div class="search is--template-border w-form">
      <input
        class="search_input w-input"
        autofocus
        v-model="frameUrl"
        placeholder="Frame URL"
        maxlength="256"
        dark
      />
      <input type="submit" value="" class="search_button-3 w-button" />
    </div>
    <a
      class="icon-btn is--template-border w-inline-block"
      @click="getFramesId()"
    >
      <img
        src="../images/refresh-button.svg"
        loading="lazy"
        alt=""
        class="icon-img is--large"
      />
    </a>
  </div>
</template>

<script>
export default {
  name: "FrameUrlComp",
  props: {},
  data: () => ({
    frameUrl: "",
  }),
  mounted () {
    this.frameUrl = this.$route.query.lobjID;
    this.$emit('on-change-frameUrl', this.frameUrl);
    if (this.frameUrl) this.getFramesId();
  },
  methods: {
    getFramesId: async function () {
      const loader = this.$loading.show();
      const frameIds = [];
      try {
        this.$emit('on-change-frameUrl', this.frameUrl);
        const url = new URL(this.frameUrl);
        const response = await this.$axios({
          url: url.pathname,
          baseURL: url.origin,
        });
        const div = document.createElement("div");
        div.insertAdjacentHTML("afterbegin", response.data);
        const lobj = div.getElementsByClassName("lobj-contet-section");
        //get all the frames inside of a given learning objective
        const allFrames = lobj[0].querySelector(
          ":scope > .grid-frame-w"
        ).children;
        for (let i = 0; i < allFrames.length; i++) {
          //get the frame ID based on frame title. No > so it searches all the descendants
          const frameID = allFrames[i]
            .querySelector(":scope  .frame_heading, .frame_heading-2")
            .textContent.replace(/ /g, "-")
            .replace(/^[^a-z]+|[^\w:.-]+/gi, "");
          allFrames[i].id = frameID;
        }

        allFrames.forEach((frame) => {
          if (frame.id) {
            frameIds.push(frame.id);
          }
        });
      } catch (error) {
        console.log(error);
        alert(error);
        loader.hide();
      }
      this.$emit("on-load-frameIds", frameIds);
      loader.hide();
    },
  },
};
</script>